
#app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    text-align: center;
    color: #2c3e50;
    margin-top: 60px;
}
#left{
    width:60%;
    float:left;
}
#right{
    width:40%;
    float:right;
    overflow: scroll;
    height: 90vh;
}

#NGList{
}
.reg_NG{
    background-color: #FFF5EE;
    padding:5px;
    margin:5px;
    border-style: #FF1493 solid 5px;
    border-radius: 10px;
}
.inner_NG{
    background-color: #FFF0F5;
    border-radius: 10px;
    padding: 5px;
    margin:5px;
}
ul, li{
    list-style-type: none;
    margin:5px;
}
.NG{
    border:#eeeeee solid 2px;
    background-color: #FFE4E1;
    font-size: 20px;
}
.checkbox{
    font-size:30px;
    margin: 4px;
    margin-bottom: 4px;
    background-color: #FFE4E1;
    border-radius:4px;
    font-weight: normal;
}
.show_NG{
    background-color: #FFF5EE;
    padding:5px;
    margin:5px;
    border-style: #FF1493 solid 5px;
    border-radius: 10px;
}
table{
    text-align: left;
    font-size: 20px;
}
.NG_list{
    text-align:left;
}
.label{
    font-weight:bold;
    font-size:20px;
    border-bottom:#aaaaaa dashed 2px;
}
.value{
    font-size:25px;
}
ul{
    padding-left:5px;
}
a{
    font-size:20px;
}


